import { Component, h, State, Listen, Event, EventEmitter } from '@stencil/core/internal';

@Component({
  tag: 'rf-date-range-picker',
  styleUrl: './date-range-picker.css',
  shadow: true,
})
export class DateRangerPicker {
  @State() show = false;

  open() {
    this.show = !this.show;
  }

  @Listen('hide_cal', { target: 'body' })
  hide(event: CustomEvent) {
    this.show = !event.detail;
  }

  @State() firstDate: Date = null;
  @State() lastDate: Date = null;

  @Listen('send_first_date', { target: 'body' })
  getFirstDate(event: CustomEvent) {
    this.firstDate = event.detail;
    this.lastDate = null;
  }

  @Listen('send_second_date', { target: 'body' })
  getSecondDate(event: CustomEvent) {
    this.lastDate = event.detail;
  }

  @Listen('close_cal', { target: 'body' })
  closeSecondDate(event: CustomEvent) {
    this.lastDate = event.detail;
    this.show = false;
  }

  @Listen('send_guest', { target: 'body' })
  getGuest(event: CustomEvent) {
    this.guests = event.detail;
  }

  @State() guests: number;

  handleClick() {
    this.book.emit({
      firstDate: this.firstDate?.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }),
      lastDate: this.lastDate?.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }),
      guests: this.guests,
    });
  }
  @Event({ bubbles: true, composed: true }) book: EventEmitter<{ firstDate: string; lastDate: string; guests: number }>;
  render() {
    let first;
    let second;

    if (this.lastDate == null) {
      first = this.firstDate;
      second = this.lastDate;
    } else if (this.firstDate < this.lastDate) {
      first = this.firstDate;
      second = this.lastDate;
    } else {
      first = this.lastDate;
      second = this.firstDate;
    }
    return (
      <div class="container">
        <div class="date-input" onClick={this.open.bind(this)}>
          <span>Check in: </span>
          <input type="text" value={first?.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }) ?? ''} placeholder="Select date" />
          <span>Check out: </span>
          <input type="text" value={second?.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }) ?? ''} placeholder="Select date" />
        </div>
        {this.show ? (
          <div class="calendar">
            <rf-first></rf-first>
            <rf-second></rf-second>
          </div>
        ) : null}
        <rf-guest></rf-guest>
        <button onClick={this.handleClick.bind(this)}>Book Now</button>
      </div>
    );
  }
}
